.link {
    text-decoration: none;
  }
  
  .list {
    list-style: none;
  }
  
  .address {
    font-style: normal;
  }
  
  body {
    font-family: var(--main-font);
    margin: 0;
  }
  
  h1,
  h2,
  h3,
  h4,
  h5,
  h6,
  p {
    margin: 0;
  }
  
  ul,
  ol {
    margin: 0;
    padding: 0;
  }
  
  img {
    display: block;
    max-width: 100%;
    height: auto;
  }

  
